
@import '/static/css/style.css';
@import '/static/css/reset.css';
body,html{
line-height:1;
font-weight:200;
font-family:"PingFang SC","STHeitiSC-Light","Helvetica-Light";
}
#app .tab{
  position: fixed;
  left: 0;
  bottom: 0;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  z-index: 999;
  width:100%;
  height:40px;
  line-height:40px;
  border-top:2px solid rgba(7,17,21,0.1);
  font-weight: 700;
  background-color: rgb(7, 17,27);
}
#app .tab .tab-item{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  text-align:center;
}
#app .tab .tab-item a{
  display:block;
  text-decoration:none;
  
  color:#fff;
  font-weight: 700;
}
#app .tab .tab-item .active{
  color:rgb(0,160,220);
}





